import { UPDATE_CONTACT } from "../utils/actionsTypes";

export default function receiveReaction(action) {
  return (dispatch, getState) => {
    const user = getState().user;
    const message = action.payload.message;
    let isReactionMadeBySender = user.id === message.senderId ? true : false;
    let contactId = isReactionMadeBySender
      ? message.receiverId
      : message.senderId;
    const filteredContacts = getState().contacts.contacts.filter(
      (c) => c.id === contactId
    );
    if (filteredContacts.length < 1) return;

    let contact = structuredClone(filteredContacts[0]);
    for (let i = 0; i < contact.messages.length; i++) {
      if (contact.messages[i].uuid === message.uuid) {
        if (!isReactionMadeBySender){
          contact.messages[i].senderReaction = message.senderReaction;
        }else{
          contact.messages[i].receiverReaction = message.receiverReaction;
        }
        break;
      }
    }
    dispatch({type:UPDATE_CONTACT , payload : contact });
  };
}
