import "../css/login.css";
import usernameIcon from "../images/user-alt-1-svgrepo-com.svg";
import passwordIcon from "../images/lock-keyhole-minimalistic-svgrepo-com.svg";
import logo from "../images/chat-svgrepo-com (1).svg";
import { useState , useRef , useEffect } from "react";
import { useDispatch } from "react-redux";
import loginRequest from "../networkRequests/loginRequest";
import signupRequest from "../networkRequests/signupRequest";
export default function Login() {

    const [haveAccount , setHaveAccount] = useState(true);
    const [error , setError] = useState({error:false,message : "There is some error" });
    const [msg , setMsg] = useState({ haveMsg : false , msg : "" } );
    const [isLoading,setIsLoading] = useState(false);
    const loginBtnRef = useRef();
    const signupBtnRef = useRef();
    const usernameRef = useRef();
    const passwordRef = useRef();
    const passwordConrifmRef = useRef();
    const dispatch = useDispatch();


    useEffect(()=>{
        const loginButton =loginBtnRef.current; 
        const signupButton = signupBtnRef.current;
        if( loginButton )
            loginButton.addEventListener('click' , login );
        if( signupButton )            
            signupButton.addEventListener('click' , signup );

        return ()=>{
            if( loginButton )
                loginButton.removeEventListener('click', login );
            if( signupButton )
                signupButton.removeEventListener('click', signup );
        }
    });

    function login(){
        const username = usernameRef.current.value;
        const password = passwordRef.current.value;
        setIsLoading(true);
        dispatch( loginRequest( username , password , (errMessage)=>{
          setIsLoading(false);  
          //setError({error:true,message : errMessage })
            
        } ) );

    }
    function signup(){
        const username = usernameRef.current.value;
        const password = passwordRef.current.value;
        setIsLoading(true);
        dispatch( signupRequest( username , password , (errMessage)=>{
          if( errMessage ){
            setError({error:true,message : errMessage })
            return;
          }
          setMsg({ haveMsg : true ,msg: "You signed up successfully , Now please Login" });
          setIsLoading(false); 
          setHaveAccount(true);
          
        }));
        //dispatch signup
    }

  return (
    <div className="login-screen">
       <nav>
        <div className="flex jc-center ai-center g-8">
        <img src={logo}  alt="" className="logo" /><h4  style={{margin:0}}>Chatty</h4>
        </div>
        <div>
        <button className="form-btn float-on-hover ">Login</button>
        <button className="form-btn form-btn-sec ml-16 float-on-hover ">Signup</button>
        </div>
           
       </nav>
      <div className="flex jc-center ai-center" style={{width:"100%",height:"100%"}}>
          { 
            //haveAccount ?  
            //<LoginForm/> :
            //<SignupForm/> 
        } 

<div className="form">
        <div className="flex jc-center ai-center g-8">
        <img src={logo} alt=""
         className="logo " 
         /><h4  style={{margin:0}}>Chatty</h4>
        </div>
          {
            !error.error && !isLoading &&  !msg.haveMsg  && <h5 style={{margin:0}}>Welcome</h5>            
          }
          {
            isLoading && <div>Loading</div>
          }

          {
              error.error && <div className="error-message">{error.message}</div>
          }

          {
             msg.haveMsg && <div className="flex jc-center ai-center" style={{textAlign:"center"}}>{msg.msg}</div>
          }  

          <FormInput icon={usernameIcon} inputRef={usernameRef} placeholder="username" />
          <FormInput icon={passwordIcon} inputRef={passwordRef} placeholder="password" />
          { !haveAccount && <FormInput icon={passwordIcon} inputRef={passwordConrifmRef} placeholder="confirm password" /> }

           {
           haveAccount ?   
            (<>
              <button className="form-btn float-on-hover" ref={loginBtnRef} >Login</button>
              <p className="create-account-text">Don't have an account ?
                <div className="blue-color" onClick={()=>setHaveAccount(false)}>Create an account.</div>
              </p>
            </>)
            :
            (
              <>
              <button className="form-btn float-on-hover" ref={signupBtnRef} >Signup</button>
              <p className="create-account-text">
                Already have an account ?
                <div className="blue-color" onClick={()=>setHaveAccount(true)}>Click here to login.</div>
              </p>
              </>
            )

            }
        </div>
           
      </div>
    </div>
  );

function FormInput(props) {
  return (
    <div className="login-input-container">
      <div className="input-icon-container">
        <img src={props.icon} alt="" className="input-icon" />
      </div>
      <input
        ref={props.inputRef}
        type="text"
        placeholder={props.placeholder}
        className="login-input"
      />
    </div>
  );
}
}


