import axios from "axios";

export default  function signup(username , password,callback) {

    const body = {
      username: username,
      password: password,
    };
    axios
      .post("/register", body, { withCredentials: true })
      .then((value) => {
        console.log(value);
        callback();
      })
      .catch((err) => {
          console.log(err);
          callback(err);
      });
  }