import { UPDATE_CONTACT } from "../utils/actionsTypes";
export default function updateMessageStatus(action) {
  return (dispatch, getState) => {

    const state = getState();
    const msg = action.payload;
    const contactArray = state.contacts.contacts.filter(c=> c.id === msg.receiverId );
    
    if( contactArray.length > 0 ){
      const contact = contactArray[0];
    
    for(let i=contact.messages.length-1;i>=0;i--){
      if( contact.messages[i].uuid === msg.uuid ){
        contact.messages[i].status = msg.status;
        break;
      }
    }

    dispatch({type:  UPDATE_CONTACT , payload : contact });
    }


  };
}
