import {
  ADD_MESSAGE_TO_CONTACT,
  ADD_NEW_CONTACT,
  ADD_RECEIVED_MESSAGE_TO_CONTACT,
  SET_CONTACTS,
  SET_CONTACT_MESSAGES,
  SET_SELECTED_CONTACT,
  UPDATE_CONTACT,
  UPDATE_MESSAGE_STATUS,
} from "../utils/actionsTypes";
const init = {
  contacts: [],
  selectedContact: undefined,
  isLoaded : false
};
export default function contactsReducer(state = init, action) {
  switch (action.type) {
    case SET_SELECTED_CONTACT:
      let selectedContactState = {
        ...state,
        selectedContact: action.payload,
      };
      return selectedContactState;
    case SET_CONTACTS:
      let contactsState = {
        selectedContact: state.selectedContact,
        contacts: action.payload,
        isLoaded : true
      };
      return contactsState;
    case SET_CONTACT_MESSAGES:
      let filteredContactArray = state.contacts.filter(
        (c) => c.id === action.payload.id
      );
      let contact = filteredContactArray[0];
      contact.messages = action.payload.messages;
      contact.hadRecivedMessages = true;

      let contacts = structuredClone(state.contacts);
      for (let i = 0; i < contacts.length; i++) {
        if (contacts[i].id === contact.id) {
          contacts[i] = contact;
          break;
        }
      }

      let newState = structuredClone(state);
      newState = {
        ...newState,
        contacts: contacts,
      };
      return newState;

    case ADD_MESSAGE_TO_CONTACT:
      let response = addMessageToContact(state, action);
      return structuredClone(response);
    case UPDATE_MESSAGE_STATUS:
      return updateMessageStatus(state, action);
    case ADD_RECEIVED_MESSAGE_TO_CONTACT:
      return receiveMessageFromAnotherContact(state, action);
    case ADD_NEW_CONTACT:
      return addNewContact(state, action);
    case UPDATE_CONTACT:
      return updateContact(state, action);
    default:
      return state;
  }
}

function updateContact(state, action) {
  let contacts = structuredClone(state.contacts);
  const updatedContact = structuredClone(action.payload);
  const newState = structuredClone(state);

  for (let i = 0; i < contacts.length; i++) {
    if (contacts[i].id === updatedContact.id) {
      contacts[i] = updatedContact;
      break;
    }
  }

  if (updatedContact.id === state.selectedContact.id) {
    return {
      ...newState,
      contacts: contacts,
      selectedContact: updatedContact,
    };
  }
  return {
    ...newState,
    contacts: contacts,
  };
}
function addNewContact(state, action) {
  let contacts = structuredClone(state.contacts);
  contacts.unshift(action.payload);
  return {
    ...state,
    contacts: contacts,
  };
}

function updateMessageStatus(state, action) {
  const filteredContactArray = state.contacts.filter(
    (c) => c.id === action.payload.contactId
  );
  let contact = structuredClone(filteredContactArray[0]);
  let messages = contact.messages;
  for (let i = 0; i < messages.length; i++) {
    if (messages[i].hasOwnProperty("uuid")) {
      if (messages[i].uuid === action.payload.uuid) {
        messages[i].status = action.payload.status;
        break;
      }
    }
  }
  contact.messages = messages;
  let contacts = updateExistingContactInContacts(state.contacts, contact);
  let newState = structuredClone(state);
  if (state.selectedContact.id === contact.id) {
    newState = {
      ...newState,
      contacts: contacts,
      selectedContact: contact,
    };
    return newState;
  }
  newState = {
    ...newState,
    contacts: contacts,
  };
  return newState;
}

function receiveMessageFromAnotherContact(state, action) {
  const filteredContactArray = state.contacts.filter(
    (c) => c.id === action.payload.senderId
  );
  let contact = structuredClone(filteredContactArray[0]);
  if (contact.hasOwnProperty("messages")) {
    contact.messages.push(action.payload);
  } else {
    contact.messages = [];
    contact.messages.push(action.payload);
  }

  //let contacts = updateExistingContactInContacts(state.contacts, contact);
  let contacts = structuredClone(state.contacts);
  contacts = contacts.filter((c) => c.id !== contact.id);
  contacts.unshift(contact);
  let newState = structuredClone(state);
  if (
    state.selectedContact !== undefined &&
    state.selectedContact.id === contact.id
  ) {
    newState = {
      ...newState,
      contacts: contacts,
      selectedContact: contact,
    };
    return newState;
  }
  newState = {
    ...newState,
    contacts: contacts,
  };
  return newState;
}
function addMessageToContact(state, action) {
  const filteredContactArray = state.contacts.filter(
    (c) => c.id === action.payload.receiverId
  );
  let contact = structuredClone(filteredContactArray[0]);
  if (contact.hasOwnProperty("messages")) {
    contact.messages.push(action.payload);
  } else {
    contact.messages = [];
    contact.messages.push(action.payload);
  }

  // let contacts = updateExistingContactInContacts(state.contacts, contact);
  let contacts = structuredClone(state.contacts);
  contacts = contacts.filter((c) => c.id !== contact.id);
  contacts.unshift(contact);
  let newState = structuredClone(state);
  if (state.selectedContact.id === contact.id) {
    newState = {
      ...newState,
      contacts: contacts,
      selectedContact: contact,
    };
    return newState;
  }
  newState = {
    ...newState,
    contacts: contacts,
  };
  return newState;
}
function updateExistingContactInContacts(contactList, contact) {
  let contacts = structuredClone(contactList);
  for (let i = 0; i < contacts.length; i++) {
    if (contacts[i].id === contact.id) {
      contacts[i] = contact;
      break;
    }
  }
  return contacts;
}
