import loginUserPic from "../images/login-user.png";
import messageStatus from "../utils/messageStatus";
import { useSelector } from "react-redux";
export default function Contacts(props) {
  const contact = props.contact;

  const user = useSelector((state) => state.user);

  let numberOfUnreadedMessages;
  const hasLastMessage =
    contact.hasOwnProperty("messages") &&
    contact.messages instanceof Array &&
    contact.messages.length > 0;
  let lastMessage = undefined;
  if (hasLastMessage) {
    lastMessage = contact.messages[contact.messages.length - 1].messageText;
    numberOfUnreadedMessages = 0;
    for (let i = contact.messages.length - 1; i >= 0; i--) {
      if (contact.messages[i].senderId === user.id) {
        break;
      }
      if (
        contact.messages[i].hasOwnProperty("status") &&
        contact.messages[i].status < messageStatus.READ &&
        contact.messages[i].receiverId === user.id
      ) {
        numberOfUnreadedMessages++;
      }
    }
  }
  const contactSelectedClass = props.isSelected ? " contact-selected " : " ";

  const showMessagesNotification = !props.isSelected && (numberOfUnreadedMessages > 0);
  console.log(contact.username ,props.isSelected , numberOfUnreadedMessages  );

  return (
    <div
      className={"contact " + contactSelectedClass}
      onClick={() => props.onSelect(props.contact)}
    >

      <div className="flex g-16">
      <img src={loginUserPic} alt="" className="contact-profile-img" />

<div className="name-and-last-mesg-container">
  <div className="contact-name ">{props.contact.username}</div>
  <div className="flex">
    {hasLastMessage && <div className="last-msg">{lastMessage}</div>}
  </div>
</div>
      </div>

      { 
      showMessagesNotification  &&  (<div className="unreaded-msgs-notification"></div>)
        }
    </div>
  );
}
