  import socketService from "../services/socketService";
import { SET_CONTACT_MESSAGES, UPDATE_CONTACT } from "../utils/actionsTypes";
import messageStatus from "../utils/messageStatus";
  export default function getChats(action) {
    return (dispatch, getState) => {
      
      const contactToGetMessagesFor = action.contact;
      const contact = {
        id: contactToGetMessagesFor.id,
      };

        const contactHasMessages =  contactToGetMessagesFor.hasOwnProperty("messages") &&  contactToGetMessagesFor.messages.length > 1
      if ( !contactHasMessages ) {
        if (socketService.isSocketConnected)
          socketService.getSocket().emit("getChats", contact, function (messages) {
            for(let i=0;i<messages.length;i++)
              messages[i].status = messageStatus.READ
            
            const payload = {
              id: contact.id,
              messages: messages,
            };
            console.log(contact);
            dispatch({ type: SET_CONTACT_MESSAGES, payload: payload });

            updateMessagesStatus(messages);


          });
      }else{

        updateMessagesStatus(contactToGetMessagesFor.messages);

      }

      function updateMessagesStatus(messages){
        const state = getState();
        let selectedContact = state.contacts.contacts.filter(c=>c.id === state.contacts.selectedContact.id );
        selectedContact.messages = messages;
        const [newContact , updatedMessages ] = markMessagesAsRead(selectedContact);
        if( updatedMessages.length > 0 )
        socketService.getSocket().emit("updateBulkMessages" , {messages : updatedMessages} , (response)=>{
          dispatch({ type: UPDATE_CONTACT, payload: newContact });
        })
        
      }

      function markMessagesAsRead(contact){
        const user = getState().user;
        if( contact.hasOwnProperty("messages") ){
          let updatedMessagesArray = [];
          for(let i=0;i<contact.messages.length;i++){
            if( contact.messages[i].hasOwnProperty("status") ){
              if(  contact.messages[i].senderId !== user.id  && contact.messages[i].status < messageStatus.READ  ){
                contact.messages[i].status = messageStatus.READ;
                const updatedMsgObj = {
                  ...contact.messages[i],
                  status : messageStatus.READ,
                }
                updatedMessagesArray.push(updatedMsgObj);
              }
            }
            
          }
          return [ contact , updatedMessagesArray  ];


        }
      }


      //mark messages as read when they arrive
    };
  }

 