import Contact from "./Contact";
import { useRef, useState, useEffect } from "react";
import { useSelector } from "react-redux";
import axios from "axios";
import arrowLeft from "../images/arrow-left.svg";
import { SET_SELECTED_CONTACT } from "../utils/actionsTypes";
import { useDispatch } from "react-redux";
import getChatsAction from "../actions/getChatsAction";

export default function Contacts(props) {
  const dispatch = useDispatch();
  const [isSearching, setIsSearching] = useState(false);
  const [searchResult, setSearchResult] = useState([]);
  const [isLoading, setIsLoading] = useState(false);
  const contacts = useSelector((state) => state.contacts.contacts);
  const selectedContact = useSelector(
    (state) => state.contacts.selectedContact
  );
  const user = useSelector((state) => state.user);
  const inputRef = useRef();

  useEffect(() => {
    if (isSearching && inputRef && inputRef.current) inputRef.current.focus();
  });

  if (
    (!contacts instanceof Array &&
      contacts.length > 0 &&
      selectedContact === undefined) ||
    contacts === undefined
  ) {
    return <div>Loading</div>;
  }

  if (isSearching) return addNewContactView();

  return contactsView();

  function back() {
    setIsSearching(false);
    inputRef.current.value = "";
  }

  function showSeachForContact() {
    setIsSearching(true);
    if (inputRef && inputRef.current) inputRef.current.focus();
  }

  function search(e) {
    let value = inputRef.current.value.trim();
    if (value === "") return;
    setIsLoading(true);
    const url = "/getContacts?contactStart=" + value;
    axios
      .get(url)
      .then((res) => {
        setIsLoading(false);
        setSearchResult(res.data);
      })
      .catch((err) => {
        setIsLoading(false);
        console.error(err);
      });
  }

  function addNewContactView() {
    return (
      <div className="contacts-container">
        <div className="contacts">
          <div className="flex g-8 jc-center ai-center">
            <div className="back-button">
              <img
                src={arrowLeft}
                alt=""
                onClick={() => back()}
                className="back-icon"
              />
            </div>
            <input
              type="text"
              placeholder="search"
              ref={inputRef}
              className="contacts-search-input"
              onChange={search}
            />
          </div>

          {!isLoading &&
            searchResult.length !== 0 &&
            searchResult.map((contact) => (
              <Contact
                key={"search_contact_" + contact.id}
                setSelectedContact={props.setSelectedContact}
                contact={contact}
                onSelect={addNewContact}
              />
            ))}

          {isLoading && <div className="loading-contacts">Loading</div>}

          {!isLoading && searchResult.length === 0 && (
            <div className="loading-contacts">No result found</div>
          )}
        </div>
      </div>
    );
  }

  function contactsView() {
    return (
      <div className="contacts-container">
        {user.isUserLoaded && (
          <div className="flex ai-center p-16">
            <div className="user-name">User : {user.username}</div>
          </div>
        )}

        <div className="contacts">
          <div className="add-chat-btn-container" onClick={showSeachForContact}>
            <div className="add-chat-btn">+</div>
          </div>

          {contacts.length > 0 &&
            selectedContact !== undefined &&
            contacts.map((contact) => (
              <Contact
                key={"contact_" + contact.id}
                setSelectedContact={props.setSelectedContact}
                contact={contact}
                isSelected={selectedContact.id === contact.id}
                onSelect={onSelectContact}
              />
            ))}
        </div>
      </div>
    );
  }

  function addNewContact(contact) {
    dispatch({ type: SET_SELECTED_CONTACT, payload: contact });
  }

  function onSelectContact(contact) {
    dispatch({ type: SET_SELECTED_CONTACT, payload: contact });
    dispatch(getChatsAction({ contact: contact }));
    //props.setSelectedContact(props.contact);
  }
}
