import socketService from "../services/socketService";
import {
  ADD_NEW_CONTACT, SET_SELECTED_CONTACT,
  UPDATE_CONTACT
} from "../utils/actionsTypes";
import messageStatus from "../utils/messageStatus";
export default function receivedMessageAction(action) {
  return (dispatch, getState) => {
    const msg = action.payload;
    const state = getState();

    const selectedContact = state.contacts.selectedContact;
    const filteredContactArray = state.contacts.contacts.filter(
      (c) => msg.senderId === c.id
    );

    if (filteredContactArray.length === 0) {
      if (socketService.isSocketConnected()) {
        socketService
          .getSocket()
          .emit("getContactInfo", { id: msg.senderId }, (user) => {
            user.messages = [msg];
            dispatch({ type: ADD_NEW_CONTACT, payload: user });
            dispatch({ type: SET_SELECTED_CONTACT, payload: user });
            markMessageAsReceivedOrDelevered(msg, user);
          });
        return;
      }
    }

    let contact = structuredClone(filteredContactArray[0]);
    contact.messages.push(msg);
    dispatch({ type: UPDATE_CONTACT, payload: contact });
    if (socketService.isSocketConnected()) {
      //dispatch({type:ADD_RECEIVED_MESSAGE_TO_CONTACT , payload: msg });
      markMessageAsReceivedOrDelevered(msg, selectedContact);
    } else {
    }

    function markMessageAsReceivedOrDelevered(msg, selectedContact) {
      msg.status = messageStatus.DELEVERED;
      if (
        selectedContact !== undefined &&
        msg !== undefined &&
        msg.senderId === selectedContact.id
      ) {
        msg.status = messageStatus.READ;
      }

      if (socketService.isSocketConnected())
        socketService.getSocket().emit("updateMessageStauts", msg);
    }
  };
}
