import readTick from "../images/double-tick-white.svg";
import deleveredTick from "../images/double-tick.svg";
import sentTick from "../images/tick.svg";
import messageStatus from "../utils/messageStatus";

import like from "../images/like.gif";
import love from "../images/love.gif";
import haha from "../images/haha.gif";
import wow from "../images/wow.gif";
import sad from "../images/sad.gif";
import angry from "../images/angry.gif";
import { useRef, useEffect } from "react";
import { useDispatch } from "react-redux";

import messageReaction from "../utils/messagReactions";
import addReactionToMessageAction from "../actions/addReactionToMessageAction";
import { ADD_REACTION_TO_MESSAGE } from "../utils/actionsTypes";
import { useSelector } from "react-redux";

export default function Message(props) {
  const dispatch = useDispatch();
  const user = useSelector((state) => state.user);
  let msg = props.msg;
  const date = new Date(msg.createdAt);
  const emojiRef = useRef();
  const msgRef = useRef();

  const hours =
    date.getHours().toString().length === 1
      ? `0${date.getHours()}`
      : date.getHours();
  const minutes =
    date.getMinutes().toString().length === 1
      ? `0${date.getMinutes()}`
      : date.getMinutes();
  const msgDate = `${hours}:${minutes}`;

  function handleOnClickEvent(event){
    if (endDate && Date.now() - endDate > timeoutLimit)
    if (event.target !== emojiRef.current) {
      if( msgRef.current && emojiRef.current )
          emojiRef.current.classList.add("hidden");
    }
  }

  let timoutId = 0;
  let startDate;
  let endDate;
  const timeoutLimit = 400; //1000ms equals 1second

  useEffect(() => {

    if (msgRef.current && emojiRef.current) {
      document.addEventListener("click", handleOnClickEvent   );
      msgRef.current.onmousedown = function (e) {
        // eslint-disable-next-line react-hooks/exhaustive-deps
        startDate = Date.now();
        // eslint-disable-next-line react-hooks/exhaustive-deps
        timoutId = setTimeout(() => {
          emojiRef.current.classList.remove("hidden");
        }, timeoutLimit);
      };
      msgRef.current.onmouseup = function (e) {
        // eslint-disable-next-line react-hooks/exhaustive-deps
        endDate = Date.now();
        let result = endDate - startDate;
        if (result < timeoutLimit) {
          clearTimeout(timoutId);
        }
        //endDate=0;
        startDate=0;
      };
    }

    return ()=>{
      document.removeEventListener("click", handleOnClickEvent );
    }

  });

  if (msg.senderId === props.selectedContactId)
    return receiverMessage()
  else return senderMessage();


  function receiverMessage() {
    return (
      <div className="flex jc-start p-relative " ref={msgRef}>

        <div className="flex ai-start column m-8 ml-16 p-relative" >
        {emojies(emojiRef)}
        <div className="reactions-left">
          {msg.senderReaction && getReaction(msg.senderReaction)}
          { msg.receiverReaction && getReaction(msg.receiverReaction)}
        </div>
          <div className="msg-left column">
            <div className="msg">{msg.messageText}</div>
            <div className="flex ai-center jc-start">
              <div className="msg-date">{msgDate}</div>
            </div>
          </div>
        </div>
      </div>
    );
  }
  function senderMessage() {
    return (
      <div className="flex jc-flex-end p-relative " ref={msgRef}>
         {emojies(emojiRef)}
        <div className="reactions-right">
          { msg.senderReaction && getReaction(msg.senderReaction)}
          { msg.receiverReaction && getReaction(msg.receiverReaction)}
        </div>
        <div className="flex ai-end column m-8 mr-16" >
          <div className="msg-right column">
            <div className="msg">{msg.messageText}</div>
            <div className="flex ai-center jc-end">
              <div className="msg-date msg-date-right">{msgDate}</div>
              {msg.status && showMessageStatus()}
            </div>
          </div>
        </div>
      </div>
    );
  }

  function showMessageStatus() {
    if (msg.status) {
      switch (msg.status) {
        case messageStatus.SENT:
          return <img src={sentTick} alt="" className="tick" />;
        case messageStatus.DELEVERED:
          return <img src={deleveredTick} alt="" className="tick" />;
        case messageStatus.READ:
          return <img src={readTick} alt="" className="tick" />;
        default:
          <img src={sentTick} alt="" className="tick" />;
      }
    }
  }

  function getReaction(reactionId) {
    switch (reactionId) {
      case messageReaction.ANGRY:
        return <img src={angry} className="emoji" alt="Angry emoji" />;
      case messageReaction.HAHA:
        return <img src={haha} className="emoji" alt="Angry emoji" />;
      case messageReaction.LIKE:
        return <img src={like} className="emoji" alt="Angry emoji" />;
      case messageReaction.LOVE:
        return <img src={love} className="emoji" alt="Angry emoji" />;
      case messageReaction.SAD:
        return <img src={sad} className="emoji" alt="Angry emoji" />;
      case messageReaction.WOW:
        return <img src={wow} className="emoji" alt="Angry emoji" />;
      default:
        return <img src={like} className="emoji" alt="Angry emoji" />;
    }
  }

  function submitMessageReaction(reaction) {
    let payload = {};
    payload.message = msg;
    payload.contactId = props.selectedContactId;
    if (user.id === msg.senderId) {
      payload.message.senderReaction = reaction;
    } else {
      payload.message.receiverReaction = reaction;
    }

    dispatch(
      addReactionToMessageAction({
        type: ADD_REACTION_TO_MESSAGE,
        payload: payload,
      })
    );
    emojiRef.current.classList.add("hidden");
  }

  function emojies(emojiRef) {
    return (
      <figure className="emojies-figure hidden" ref={emojiRef}>
        <img
          src={like}
          onClick={() => {
            submitMessageReaction(messageReaction.LIKE);
          }}
          className="emoji"
          alt="Like emoji"
        />
        <img
          src={love}
          onClick={() => {
            submitMessageReaction(messageReaction.LOVE);
          }}
          className="emoji"
          alt="Love emoji"
        />
        <img
          src={haha}
          onClick={() => {
            submitMessageReaction(messageReaction.HAHA);
          }}
          className="emoji"
          alt="Haha emoji"
        />
        <img
          src={wow}
          onClick={() => {
            submitMessageReaction(messageReaction.WOW);
          }}
          className="emoji"
          alt="Wow emoji"
        />
        <img
          src={sad}
          onClick={() => {
            submitMessageReaction(messageReaction.SAD);
          }}
          className="emoji"
          alt="Sad emoji"
        />
        <img
          src={angry}
          onClick={() => {
            submitMessageReaction(messageReaction.ANGRY);
          }}
          className="emoji"
          alt="Angry emoji"
        />
      </figure>
    );
  }
}
