import axios from "axios";
import { SET_CURRENT_USER, UNAUTHORIZED_USER } from "../utils/actionsTypes";

export default  function loginRequest(username , password , callback ) {
    console.log("WWWWWWWww");
    return (dispatch , getState )=>{
        console.log("asdasdad");
        const body = {
            username: username,
            password: password,
          };
          axios
            .post("/login", body, { withCredentials: true })
            .then((user) => dispatch({ type: SET_CURRENT_USER, payload: user }))
            .catch((err) =>  {
              console.log(err)
              if( err.response.status === 401 ){
                callback("Wrong username or password")
              }else{
                callback("Something went wrong , please try again")
              }
            } );
            //dispatch({ type: UNAUTHORIZED_USER })
    }

  }