import { useRef , useEffect } from "react";
import { useSelector } from "react-redux";
import sendMessageAction from "../actions/sendMessageAction";
import { useDispatch } from "react-redux";
import messageStatus from "../utils/messageStatus";
import profilePhoto from "../images/login-user.png";
import sendIcon from "../images/send.svg";
import Message from "./Message";
export default function Chat(props) {
  const inputRef = useRef();
  const messagesRef = useRef();
  const dispatch = useDispatch();
  const selectedContact = useSelector(
    (state) => state.contacts.selectedContact
  );

  const user = useSelector( state =>state.user);

  useEffect(()=>{
      if( selectedContact !== undefined)
    messagesRef.current.scroll(0,messagesRef.current.scrollHeight);
  })
  
  if( selectedContact === undefined )
    return (<div className="chat" style={{textTransform:"capitalize",display:"flex",justifyContent:"center",alignItems:"center",fontSize:"2rem"}}>no Chat to show</div>);

  return (
    <div className="chat">
        <div className="chat-header-container">
            <div className="chat-header">
            <img src={profilePhoto} alt="" className="profile-photo" />
            <div className="profile-name">{selectedContact.username}</div>
            </div>
        </div>
      <div className="messages-container">
        <div className="messages" ref={messagesRef}>
          {selectedContact !== undefined &&
            selectedContact !== null &&
            selectedContact.messages !== undefined &&
            selectedContact.messages !== null &&
            selectedContact.messages.map((msg) => {
              return <Message key={'message_'+msg.uuid} msg={msg} selectedContactId={selectedContact.id} />
            })}
        </div>

      </div>
        <div className="input-container">
        <input
        type="text"
        className="input-text float-on-hover"
        ref={inputRef}
        onKeyDown={onKeyDownEvent}
        placeholder="Type a message..."
      />
      <div className="send-button float-on-hover" onClick={sendMessage} onMouseDown={sendMessage}>
          <img src={sendIcon} alt="Send Message" className="send-icon"/>
      </div>
        </div>
    </div>
  );

  function onKeyDownEvent(e) {
    if (e.code !== "Enter") return;
    sendMessage();

  }
  function sendMessage(){
    const value = inputRef.current.value;
    if (value !== "") {
      const msgBody = {
        uuid: crypto.randomUUID(),
        messageText: value,
        receiverId: selectedContact.id,
        type: "text",
        createdAt: Date.now(),
        status: messageStatus.OUTGOING,
        senderId : user.id
      };

      dispatch(sendMessageAction({ payload: msgBody }));
      //props.sendMessage(msgBody);
    }

    inputRef.current.value = "";
  }
}


