import socketService from "../services/socketService";
import { ADD_MESSAGE_TO_CONTACT,ADD_NEW_CONTACT, UPDATE_CONTACT } from "../utils/actionsTypes";
export default function sendMessageAction(action) {
  return (dispatch, getState) => {

    const msg = action.payload;

    const state = getState();

    let contactExists = false;
    let contacts = state.contacts.contacts;
    let selectedContact = state.contacts.selectedContact;

    contacts.forEach( contact =>{
      if( contact.id === selectedContact.id )
        contactExists = true;      
    });

    if( ! contactExists ){
      dispatch({type:ADD_NEW_CONTACT , payload: selectedContact });
    }


    if( socketService.isSocketConnected() ){
      dispatch({type:ADD_MESSAGE_TO_CONTACT , payload: msg });
      socketService.getSocket().emit("message", msg, (data) => {
        const contacts = getState().contacts.contacts;
        const contactArray = contacts.filter(c=>c.id ===  selectedContact.id );
        let contact = contactArray[0];
        for(let i=0;i<contact.messages.length;i++){
          if( contact.messages[i].uuid === data.uuid ){
            contact.messages[i].status = data.status;
            break;
          }
        }
        dispatch({type:UPDATE_CONTACT , payload: contact });
      });
    }


  };
}
