import io from "socket.io-client";
let socket = null;
let isInitialized = false;
const initializeSocket = (isLoggedIn , callback ) => {
  if (isLoggedIn && !socket && !isInitialized) {
    socket =io("");
    // Add event listeners or other socket operations here
    socket.on("connect", () => {
      console.debug("Connected to the socket server");
      callback();
    });
    isInitialized = true;
  } else if (!isLoggedIn && socket) {
    socket.disconnect();
    socket = null;
    isInitialized = false;
  }
};

const isSocketInitialized = () => isInitialized;

const isSocketConnected = () => {
  if (socket) {
    return socket.connected;
  }
  return false;
};

const getSocket = () => socket;
const socketService = { initializeSocket, isSocketInitialized, isSocketConnected, getSocket };
export default socketService;



