import { UPDATE_CONTACT } from "../utils/actionsTypes";
export default function updateBulkMessageStatus(action) {
  return (dispatch, getState) => {

    const state = getState();
    if( action.payload.error )
        return;
    const messages = action.payload.messages;
    const contactArray = state.contacts.contacts.filter(c=> c.id === messages[0].receiverId );
    
    if( contactArray.length > 0 ){
      const contact = contactArray[0];
      const contactMessages = [...contact.messages];
    for(let i=messages.length-1;i>=0;i--){
        for(let j=0;j<contactMessages.length;j++){
            if( contactMessages[j].uuid === messages[i].uuid ){
                contactMessages[j].status = messages[i].status;
            }
        }
    }

    contact.messages = contactMessages;

    dispatch({type:  UPDATE_CONTACT , payload : contact });
    }


  };
}
