import React, { useEffect } from "react";
import { useDispatch } from "react-redux";
import getInitialData from "../actions/getInitialData";
import receivedMessageAction from "../actions/receivedMessageAction";
import receiveReaction from "../actions/receiveReaction";
import updateBulkMessagesStatus from "../actions/updateBulkMessagesStatus";
import {
  default as updateMessageStatus,
  default as updateMessageStatusAction,
} from "../actions/updateMessageStatusAction";
import Chat from "../components/Chat";
import ChatProfile from "../components/ChatProfile";
import Contacts from "../components/Contacts";
import "../css/style.css";
import socketService from "../services/socketService";
import { useSelector } from "react-redux";
import LoadingScreen from "../components/LoadingScreen";


import logo from "../images/chat-svgrepo-com (1).svg";
import "../css/login.css";

export default function Main() {


  const isLoaded = useSelector(state=>state.contacts.isLoaded);

  const dispatch = useDispatch();

  useEffect(() => {
    const isSocketInitialized = socketService.isSocketInitialized();
    if (!isSocketInitialized) {
      socketService.initializeSocket(true, () => {
        dispatch(getInitialData());
        socketService
          .getSocket()
          .on("UPDATE_MESSAGE_STATUS", (data) =>
            dispatch(updateMessageStatusAction({ payload: data }))
          );
        socketService.getSocket().on("updateMessageStauts", (data) => {
          dispatch(updateMessageStatus({ payload: data }));
        });
        socketService.getSocket().on("message", (data) => {
          dispatch(receivedMessageAction({ payload: data }));
        });

        socketService.getSocket().on("updateBulkMessages", (data) => {
          dispatch(updateBulkMessagesStatus({ payload: data }));
        });

        socketService.getSocket().on("messageReaction", (data) => {
          dispatch(receiveReaction({ payload: data }));
        });
      });
    }
  });

  if( !isLoaded )
    return <LoadingScreen/>
    
  return (
    <>
    {
      /*
      <nav style={{background:"#2A2E35"}}>
        <div className="flex jc-center ai-center g-8" >
        <img src={logo}  alt="" className="logo" /><h4  style={{margin:0,color:"white"}}>Chatty</h4>
        </div>
        <div>
        </div>
           
      </nav>
      */
      }
      <main>
      
        {
          <Contacts />
        }
        
        {
          
          <Chat />
        }
        {  
          //<ChatProfile />
        }
    </main>
    </>
  );
}
