
import logo from "../images/chat-svgrepo-com (1).svg";

export default function LoadingScreen(){
    return(
        <div className="flex jc-center ai-center column" style={{width:"100%",height:"100%"}}>
            <div className="flex jc-center ai-center g-16">
                <img src={logo} alt="" style={{width:"80px"}} className="logo animate__animated animate__bounce animate__infinite animate__slow" />
                <h1  style={{margin:0,fontSize:"3rem"}}>Chatty</h1>
            </div>
            <h3 style={{textAlign:"center"}}>Sorry , You need to enter from a Computer Device.</h3>
        </div>
    );
}