import socketService from "../services/socketService";
import { SET_CONTACTS, SET_CURRENT_USER, SET_SELECTED_CONTACT } from "../utils/actionsTypes";
import getChatsAction from "./getChatsAction";
export default function getInitialData(action) {
  return (dispatch, getState) => {
    if( socketService.isSocketConnected() )
    socketService.getSocket().emit("getInitialData", "", function (data) {
      const contacts = data.contacts;
      dispatch({type:SET_CURRENT_USER , payload : data.user })
      dispatch({ type: SET_CONTACTS, payload: contacts });
      dispatch({ type: SET_SELECTED_CONTACT, payload: contacts[0] });
      if (contacts.length > 0) dispatch(getChatsAction({ contact: contacts[0] }));
    });

  };
}
